import { ref, unref, computed, watch, onMounted, onUnmounted } from 'vue';

/**
 * ## CSS
 *
 * 
 * `props``context`
 * `useId`class
 * 
 *
 * @param cssFunc CSS
 *
 * @example
 * ```
 * <script setup lang="ts">
 * import useCss from '@/composables/useCss'
 * import useId from '@/composables/useId'
 *
 * interface PropType {
 *   color?: "white" | "gray" | "black";
 * }
 *
 * withDefaults(defineProps<PropType>(), {
 *   color: "white",
 * });
 *
 * // 1. ID
 * const id = useId();
 * // 2. BEMclassidmodifier
 * const className = `MyComponent--${id}`
 * // 3. CSS
 * useCss(() =>
 *   // 2
 *   `.${className} .MyComponent__SomeElement {
 *     background-color: ${props.color};
 *   }`
 * )
 * </script>
 * ```
 */
export default (cssFunc: () => string) => {
  const style = computed(cssFunc);
  const styleElement = ref();

  onMounted(() => {
    styleElement.value = document.createElement('style');
    styleElement.value.type = 'text/css';
    styleElement.value.innerHTML = unref(style);
    document.head.append(styleElement.value);
  });

  if (process.client) {
    watch(
      [style],
      ([text]) => {
        if (styleElement.value) {
          styleElement.value.innerHTML = text;
        }
      },
      { immediate: true },
    );
  }

  onUnmounted(() => {
    document.head.removeChild(styleElement.value);
  });
};
